
.card {
  width: 100%;
  max-width: 720px;
  background-color: var(--c-mint-cream);
  border: 3px solid var(--c-granite);
  box-shadow: 5px 5px 0px rgba(60, 73, 63, 0.16);
  display: flex;
  flex-direction: column;
  font-family: var(--font-archive);
  color: var(--c-granite);
  font-size: 13px;
  line-height: 1.35;
  margin: 0 auto;
}

.round-bar {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 4px;
  padding: 8px;
  background: var(--c-grey-olive);
  color: var(--c-mint-cream);
  font-weight: bold;
  border-bottom: 2px solid var(--c-granite);

  .round-label {
    font-size: 0.9rem;
  }

  .round-number {
    font-size: 1.2rem;
    margin: 0 4px;
  }
}

.content-area {
  padding: 12px;
  min-height: 0;
}

.section {
  margin-bottom: 16px;

  &:last-child {
    margin-bottom: 0;
  }
}

.section-head {
  font-size: 0.95rem;
  border-bottom: 3px solid var(--c-celadon);
  display: inline-block;
  margin-bottom: 8px;
  font-weight: bold;
}

.info-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 8px;

  .info-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 4px 0;
    border-bottom: 1px dashed var(--c-ash-grey);

    &:last-child {
      border-bottom: none;
    }

    &.full-width {
      grid-column: 1 / -1;
      flex-direction: column;
      align-items: flex-start;
      gap: 4px;
    }

    .info-label {
      color: var(--c-grey-olive);
      font-size: 0.85rem;
    }

    .info-value {
      color: var(--c-granite);
      font-weight: 600;
      font-size: 0.85rem;
    }
  }
}

.stat-bars {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.attire-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 7px;
}

.attire-item {
  border: 1px solid var(--c-grey-olive);
  padding: 6px;
  font-size: 0.82rem;
  background: #fff;
}

.attire-slot {
  color: var(--c-grey-olive);
  font-size: 0.72rem;
  display: block;
  margin-bottom: 2px;
}

.pace-control {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.pace-info {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 6px;
  background: #fff;
  border: 1px solid var(--c-grey-olive);

  .pace-label {
    color: var(--c-grey-olive);
    font-size: 0.85rem;
  }

  .pace-value {
    color: var(--c-granite);
    font-weight: bold;
    font-size: 0.9rem;
  }

  .pace-detail {
    color: var(--c-grey-olive);
    font-size: 0.75rem;
  }
}

.pace-buttons {
  display: flex;
  gap: 8px;
}

.pace-btn {
  flex: 1;
  padding: 6px 10px;
  border: 1.5px solid var(--c-granite);
  background: var(--c-mint-cream);
  color: var(--c-granite);
  font-family: inherit;
  font-size: 0.82rem;
  font-weight: 600;
  cursor: pointer;
  box-shadow: 2px 2px 0px rgba(60, 73, 63, 0.16);
  transition: all 0.2s;

  &:hover {
    background: var(--c-ash-grey);
  }

  &:active {
    transform: translate(1px, 1px);
    box-shadow: 1px 1px 0px rgba(60, 73, 63, 0.16);
  }

  &.active {
    background: var(--c-celadon);
    color: var(--c-granite);
  }

  &:focus-visible {
    outline: 2px dashed var(--c-granite);
    outline-offset: 2px;
  }
}

@media (max-width: 600px) {
  .info-grid,
  .attire-list {
    grid-template-columns: 1fr;
  }
}
